.product_rating{
    display: flex;
}
.product{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    max-height: 400px; 
    min-width: 100px;
    margin: 10px;
    padding: 20px;
    width: 100%;
    background-color: white;
    z-index: 1;
}
.product_info{
    height: 100px;
    margin-bottom: 15px;
}
.product_price{
    margin-top: 5px;
}
.product > img{
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}
.product_addtobasket{
    background-color: #eebd4b;
    border: 1px solid;
    border-color: #a88734 #9c7e31 #846a29;
    border-radius: 5px;
    padding-top: 5px;
    padding-bottom: 5px;
    
    /* width: 25%; */
}
 
.product_addtobasket:hover{
background-color: #faf9f6;
cursor: pointer;

}